<template>
    <section class="not-found">
        <div class="wrap">
            404: Page Not Found
        </div>
    </section>
</template>

<script>
export default {
    name: 'NotFound',
    data() {
        return {

        }
    },
  created() {
    this.timer = setTimeout(this.redirect.bind(this), 2000)
  },
  methods: {
    redirect() {
      this.$router.push({ name: 'fth' })
    },
  }
}
</script>
<style>
  .not-found{width:100%;height:100%;float:left}
  .wrap{text-align:center;line-height:120px;float:left;width:100%;height:100%}
</style>



